<template>
	<div class="panel">
		<h2>双11期间数据统计</h2>
		<div ref="lineChart" class="chart">图表</div>
		<div class="panel-footer"></div>
	</div>
</template>

<script>
	export default {
		name: 'LinePanel',
		data() {
			return {
				list1: [],
				list2: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.lineChart);
				if(!chart){
					chart = this.$echarts.init(this.$refs.lineChart);
					this.$once('hook:beforeDestroy',()=>{
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					tooltip: {
						trigger: 'axis',
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					legend: {
						textStyle: {
							color: '#4c9bfd'
						},
						right: '10%'
					},
					grid: {
						left: "2",
						top: "30",
						right: "12",
						bottom: "10",
						show: true,
						borderColor: '#012f4a',
						containLabel: true
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: ['1日', '2日', '3日', '4日', '5日', '6日', '7日', '8日', '9日', '10日', '11日', '12日'],
						axisTick: {
							show: false
						},
						axisLabel: {
							color: "#fff",
							interval: 0
						},
						axisLine: {
							show: false
						}
					},
					yAxis: {
						type: 'value',
						axisTick: {
							show: false
						},
						axisLabel: {
							color: "#fff"
						},
						splitLine: {
							lineStyle: {
								color: '#012f4a'
							}
						}
					},
					color: ['#00f2f1', '#ed3f35'],
					series: [{
							name: '数据量',
							data: this.list1,
							type: 'line',
							smooth: true
						},
						{
							name: '购买量',
							data: this.list2,
							type: 'line',
							smooth: true
						}
					]
				};
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'expend/period.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.list1.push(resp.data[i][1]);
				}
				this.$axios.get(process.env.BASE_URL + 'expend/period2.dat').then(resp => {
					for (let i = 0; i < resp.data.length; i++) {
						this.list2.push(resp.data[i][1]);
					}
					this.drawChart();
				})
			})
		}
	}
</script>

